* {
    margin:0; padding:0; box-sizing: border-box;
  }

  li,ul,ol {
    list-style: none;
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  a:visited {
    color: inherit
  }
  
  .App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .navBar {
    background: rgb(255, 224, 46);
    width: 100%;
    margin: 0;
    .navCon {
      width: 100%;
      margin: 0;
    }
  }
  

  
  .jumbo {
    width: 640px;
    background-image: url("img/20210507135541454_8809721509012_ZW_00.jpg");
    height: 40vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 1vh auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
  }
  
  .jumbo h3 {
    font-size: 7vh;
  }
  
  .gridContainer {
    display: grid;
    width: 640px;
    /* border: solid 2px red; */
    margin: 0 auto;
    /* 그리드할거라고 정의 */
    grid-template-rows: repeat(6, 100px);
    /* ✨6로 시작해서 더보기 시 6씩 증가시키자 ^^후우... */
    /* grid-template-columns: 1fr 1fr 1fr; */
    /* 위에거랑 동일함 */
    grid-template-columns: repeat(6, 2fr);
  }
  
  
  .item {
    margin: 1px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    /* border: solid 2px grey; */
  }
  .item:hover .itemIMG {
    background-size: 110%;
  }
  .item1, .item7, .item13, .item19 {
    grid-column: span 4;
    grid-row: span 4;
  }
