//     grid-column: span 2;
//     grid-row:span 2;
//   }
//   .item6 {
//     grid-column: span 2;
//     grid-row:span 2;
//   }

  @for $i from 1 through 24  {
      .item#{$i} .itemIMG{
          background-image: url(./data#{$i}.jpg);
      }
  }

  @for $i from 2 through 6 {
      .item#{$i} {
        grid-column: span 2;
        grid-row:span 2;
      }
  }

  @for $i from 8 through 12 {
    .item#{$i} {
      grid-column: span 2;
      grid-row:span 2;
    }
}

@for $i from 14 through 18 {
    .item#{$i} {
      grid-column: span 2;
      grid-row:span 2;
    }
}

@for $i from 20 through 24 {
    .item#{$i} {
      grid-column: span 2;
      grid-row:span 2;
    }
}
  
  
  
  .itemIMG {
    width: 100%; height: 100%;
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    transition: .5s;
  }
  
  
  .hoverBox {
    position: absolute;
    transform: translate(-50%,-50%);
    top: 150%; left: 50%;
    background: rgba(255, 255, 255, 0.26);
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: .3s ease-out;
  }
  .hoverBox h3 {
    font-size: 3vh;
  }
  
  Button {
    margin: 2vw;
    color: red;
  }